.about-profit {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  padding-left: 150px;
  padding-right: 150px;
  // padding-top: 98px;
  padding-top: 200px;
  z-index: 6;
  position: relative;
  overflow: hidden;
  background: #121016;

  &_background {
    background: #121016;
    background-size: cover;
  }
  &__main-container {
    display: flex;
    max-width: 1140px;
    width: 100%;
    margin: 0 auto;
    justify-content: space-between;
  }

  &__title {
    font-family: "Raleway", Arial, sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 36px;
    line-height: 42px;
    background-image: linear-gradient(5deg, #8550f2, #ffffff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    color: transparent;
    margin-top: 0;
    padding-top: 0;
    margin-bottom: 48px;
  }

  &__main-text-container {
    display: flex;
    flex-direction: column;
    min-height: 391px;
  }

  &__text-container {
    display: grid;
    row-gap: 45px;
    column-gap: 30px;
    flex-wrap: wrap;
    grid-template-columns: 262px 262px;
    grid-template-rows: 1fr 1fr;
  }

  &__paragraph-title {
    font-family: "Raleway", Arial, sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    color: #f0f0f0;
    margin-top: 0;
    margin-bottom: 8px;
  }

  &__paragraph-title_high {
    height: 47px;
  }

  &__paragraph-text {
    font-family: "Raleway", Arial, sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #dadada;
    hyphens: manual;
    margin: 0;
  }

  &__paragraph-text_small {
    display: none;
  }

  &__animation-advantage {
    display: flex;
    background: url("./images/анимация_преимущества.png") transparent no-repeat;
    max-width: 555px;
    width: 100%;
    height: 555px;
    background-position: bottom;
    padding-bottom: 48px;
    &_start {
      background: url("./images/анимация.gif") transparent no-repeat;
    }
    &_hide {
      background: url("./images/анимация_преимущества.png") transparent
        no-repeat;
    }
  }
}

@media screen and (max-width: 1300px) {
  .about-profit {
    padding-left: 24px;
    padding-right: 24px;
    min-height: 912px;
    align-items: center;
    padding-top: 64px;
    padding-bottom: 0px;

    &__main-container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    &__main-text-container {
      padding-top: 0px;
    }

    &__title {
      margin-bottom: 48px;
      text-align: center;
    }

    &__text-container {
      row-gap: 45px;
      column-gap: 11.9%;
    }
    &__animation-cover {
      display: flex;
      max-width: 474px;
      width: 100%;
      min-height: 474px;
      width: 474px;
      height: 474px;
    }

    &__animation-advantage {
      max-width: 474px;
      width: 100%;
      height: 474px;
      background-size: contain;
    }
  }
}

@media screen and (max-width: 650px) {
  .about-profit {
    min-height: 758px;
    padding-left: 11px;
    padding-right: 11px;
    padding-top: 56px;

    &__text-container {
      row-gap: 0;
      column-gap: 5.7%;
      grid-template-columns: 166px 166px;
      grid-template-rows: 1fr 1fr;
    }

    &__title {
      font-size: 18px;
      line-height: 21px;
      margin-bottom: 32px;
    }
    &__paragraph-title {
      font-size: 14px;
      line-height: 16px;
    }
    &__paragraph-title_high {
      height: 32px;
    }

    &__paragraph-text {
      display: none;
    }
    &__paragraph-text_small {
      display: inline-block;
      font-family: "Raleway", Arial, sans-serif;
      font-style: normal;
      font-weight: 400px;
      font-size: 12px;
      line-height: 18px;
      color: #dadada;
      margin: 0;
    }
    &__animation-advantage {
      max-width: 352px;
      width: 100%;
      min-height: 352px;
    }
  }
}
